.footer {
  background-color: $bgc-secondary-color;
  padding-top: 29px;
  @media screen and (min-width: 768px) {
    top: 0%;
    padding-top: 158px;
  }
  @media screen and (min-width: 1280px) {
    padding-top: 165px;
  }
}
.footer__container {
  max-width: 280px;
  margin-right: auto;
  margin-left: auto;
  @media screen and (min-width: 768px) {
    max-width: 700px;
    display: flex;
    justify-content: baseline;
    padding-bottom: 91px;
  }
  @media screen and (min-width: 1280px) {
    padding-bottom: 97px;
    max-width: 1050px;
  }
}
.footer__container-primary {
  margin-bottom: 43px;
  @media screen and (min-width: 768px) {
    margin-bottom: 0%;
  }
}
.footer__container-secondary {
  width: 268px;
  display: inline-block;
  @media screen and (min-width: 768px) {
    margin-left: auto;
  }
  @media screen and (min-width: 1280px) {
    width: 319px;
  }
}

.footer__subtitle {
  font-family: $appendicular-font-family;
  font-size: 18px;
  line-height: $line-height-hero;
  text-transform: uppercase;
  color: $pink-color;
  margin-top: 0%;
  margin-bottom: 17px;
  @media screen and (min-width: 768px) {
    font-size: 26px;
    line-height: 30px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 1.13em;
  }
}
.footer__thumb {
  max-width: 200px;
  margin-right: auto;
  @media screen and (min-width: 768px) {
    max-width: 340px;
  }
  @media screen and (min-width: 1280px) {
    min-width: 477px;
  }
}
.footer__title {
  font-family: $appendicular-font-family;
  font-size: 34px;
  line-height: 1.14em;
  text-transform: uppercase;
  color: $red-color;
  margin: 0%;
  @media screen and (min-width: 768px) {
    font-size: 48px;
    line-height: $line-height-inf;
    letter-spacing: $letter-spacing-title;
  }
  @media screen and (min-width: 1280px) {
    font-size: 58px;
    line-height: 1.14em;
  }
}
.footer__social-list {
  display: flex;
  margin-left: auto;
  max-width: 153px;
  margin-bottom: 46px;
  @media screen and (min-width: 768px) {
    max-width: 162px;
  }
  @media screen and (min-width: 1280px) {
    max-width: 151px;
  }
}
.social-list__item:not(:last-child) {
  margin-right: 28px;
  @media screen and (min-width: 768px) {
    margin-right: 30px;
  }
  @media screen and (min-width: 1280px) {
    margin-right: 24px;
  }
}
.social-list__link {
  border-radius: 50%;
  width: 32px;
  height: 32px;
  background-color: $icon-color;
  color: $white-color;
  display: flex;
  justify-content: center;
  align-items: center;
  &:hover,
  &:focus {
    background-color: $red-color;
  }
  @media screen and (min-width: 768px) {
    width: 34px;
    height: 34px;
  }
}
.social-list__icon {
  stroke: currentColor;
  width: 19px;
  height: 19px;
  fill: currentColor;
  @media screen and (min-width: 768px) {
    width: 20px;
    height: 20px;
  }
}
.social-list__icon-secondary {
  width: 19.2px;
  height: 14.9px;
  fill: currentColor;
  @media screen and (min-width: 768px) {
    width: 20px;
    height: 20px;
  }
}

.contacts__thumb {
  width: 230px;
  margin-left: auto;
  margin-bottom: 43px;
  display: flex;
  align-items: center;
  justify-content: right;
  @media screen and (min-width: 768px) {
    width: 268px;
    margin-bottom: 0%;
  }
  @media screen and (min-width: 1280px) {
    width: 319px;
  }
}
.footer__text {
  display: inline-block;
  font-weight: $font-weight-bold;
  font-size: $font-size-card;
  line-height: $line-height-card;
  color: $title-color;
  margin-top: 0%;
  margin-bottom: 0%;
  margin-left: auto;
  @media screen and (min-width: 768px) {
    line-height: $line-height-footer;
    font-size: $font-size-header-button;
  }
  @media screen and (min-width: 1280px) {
    font-size: $font-size-nav-and-text;
    line-height: 1.875em;
    letter-spacing: 0.02em;
  }
}

.footer__contacts {
  font-weight: $font-weight-bold;
  font-size: $font-size-card;
  line-height: $line-height-card;
  color: $red-color;
  display: inline-block;

  @media screen and (min-width: 768px) {
    line-height: $line-height-footer;
    font-size: $font-size-header-button;
  }
  @media screen and (min-width: 1280px) {
    font-size: $font-size-nav-and-text;
    line-height: 1.875em;
    letter-spacing: 0.02em;
  }
}

.footer__basement {
  margin-left: auto;
  margin: auto;
  border-top: 1px solid $comma-color;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 22px;
  padding-bottom: 15px;
  @media screen and (min-width: 768px) {
    padding-top: 29px;
    padding-bottom: 18px;
  }
  @media screen and (min-width: 1280px) {
    padding-top: 10px;
    padding-bottom: 10px;
  }
}
.basement__container {
  width: 150px;
  @media screen and (min-width: 768px) {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.basement__text {
  font-weight: $font-weight-bold;
  font-size: $font-size-header-button;
  line-height: $line-height-text-card;
  text-align: center;
  color: #907e82;
  margin: 0%;
  @media screen and (min-width: 768px) {
    line-height: $line-height-footer;
  }
}
