@media screen and (max-width: 767px) {
  .gallery-color {
    width: auto;
    height: 50px;
  }
}
@media screen and (min-width: 768px) {
  .gallery-color {
    width: auto;

    height: 100px;
  }
}
@media screen and (min-width: 1280px) {
  .gallery-color {
    width: auto;
    height: 220px;
  }
}
.gallery-color {
  background-color: $bgc-secondary-color;
}

.Gallery {
  position: relative;

  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
}

.Gallery {
  @media screen and (min-width: 1280px) {
    background-image: url('../images/advantages/advantages__descktop/home-pic5-1.png');
  }
  @media (min-resolution: 2dppx) {
    background-image: (
      '../images/advantages/advantages__descktop/home-pic5-1@2x.png'
    );
  }
  background-repeat: no-repeat;
  background-size: cover;
}
.Gallery {
  @media screen and (max-width: 767px) {
    background-image: url('../images/advantages/advantages__mobile/home-pic5-1.png');
  }
  @media (min-resolution: 2dppx) {
    background-image: (
      '../images/advantages/advantages__mobile/home-pic5-1@2x.png'
    );
  }
  background-repeat: no-repeat;
}
.Gallery {
  @media screen and (min-width: 768px) {
    background-image: url('../images/advantages/advantages__tablet/home-pic5-1.png');
  }
  @media (min-resolution: 2dppx) {
    background-image: (
      '../images/advantages/advantages__tablet/home-pic5-1@2x.png'
    );
  }
  background-repeat: no-repeat;
}
@media screen and (max-width: 767px) {
  .Gallery {
    width: 320px;
    height: 122px;
    top: -50px;
  }
}
@media screen and (min-width: 768px) {
  .Gallery {
    width: 772px;

    height: 264px;
    top: -107px;
  }
}
@media screen and (min-width: 1280px) {
  .Gallery {
    width: 1280px;
    height: 430px;
    top: -175px;
  }
}
